<template>
    <div class="hello">
        <h1>{{ msg }}</h1>
    </div>
</template>

<script>
export default {
  name: 'cell',
  data () {
    return {
      msg: '多页面测试'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
.hello{
  h1{
    color: red;
  }
}
</style>




